import { getRefs } from './refs';
import {addClass, removeClass } from './classWork'

const refs = getRefs();

const scrollToTopOfPage = () => { // –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –¥–ª—è –ø–æ–¥–Ω—è—Ç–∏—è –Ω–∞–≤–µ—Ä—Ö
  if (window.pageYOffset > 0) {
    window.scrollTo(pageXOffset, 0);
  }
}

export const activateScrollButton = (e) => { // –ø—Ä–æ–≤–µ—Ä—è–µ—Ç –ø–æ–∑–∏—Ü–∏—é –Ω–∞ —ç–∫—Ä–∞–Ω–µ
  const coords = document.documentElement.clientHeight;
  if (window.pageYOffset > coords) {
    addClass(refs.scrollTopBtn, 'scroll__active');
    refs.scrollTopBtn.addEventListener('click', e => {//–¥–æ–±–∞–≤–ª—è–µ–º —Å–ª—É—à–∞—Ç–µ–ª—è –Ω–∞ –∫–Ω–æ–ø–∫—É
      e.preventDefault;
      scrollToTopOfPage(e); 
    });
  }

  if (window.pageYOffset < coords) {
    removeClass(refs.scrollTopBtn, 'scroll__active');
    refs.scrollTopBtn.removeEventListener('click', e => {//—É–¥–∞–ª—è–µ–º —Å–ª—É—à–∞—Ç–µ–ª—è —Å –∫–Ω–æ–ø–∫–∏
      e.preventDefault;
      scrollToTopOfPage(e); 
    });
  }
}