import './sass/main.scss';
import { getRefs } from './js/refs'
import { throttle } from 'throttle-debounce';
import { consts } from "./js/consts";
import { onFilmClick } from "./js/sectionFilmoteka"
import { changeHeader } from "./js/changeHeader"
import { onPaginationNavClick } from "./js/paginationNav"
import { onDevLinkClick } from "./js/footerDevelopers"
import { onLoad } from "./js/onLoad"
import { activateScrollButton } from './js/btnScrollUp'
import { changeLanguage } from './js/localization'
import './js/theme';  //—Ç–µ–º–∞
import './js/animationSVG'; //–∞–Ω–∏–º–∞—Ü–∏—è –ª–æ–≥–æ
import './js/searchPageOnBtn'  //–ø–æ–∏—Å–∫ –ø–æ –Ω–æ–º–µ—Ä—É —Å—Ç—Ä–∞–Ω–∏—Ü—ã
import './js/videoTrailer'; //—Ç—Ä–µ–π–ª–µ—Ä
// import './js/signIN';  //–∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è
// import './js/sign_IN';  //—Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è


const refs = getRefs();
onLoad(refs); //—Ñ—É–Ω–∫—Ü–∏—è –∑–∞–≥—Ä—É–∂–∞–µ—Ç –≤—Å–µ —á—Ç–æ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ —Å–¥–µ–ª–∞—Ç—å –ø—Ä–∏ –∑–∞–ø—É—Å–∫–µ
              // —Ç—É—Ç –Ω–∞—Ö–æ–¥—è—Ç—Å—è —Å–ª—É—à–∞—Ç–µ–ª–∏ –Ω–∞ –∫–Ω–æ–ø–∫–∏ –∏ –∏–Ω–ø—É—Ç—ã —à–∞–ø–∫–∏
refs.filmsSection.addEventListener('click', e => {
  e.preventDefault;
    onFilmClick(e); //from "./js/sectionFilmoteka" –∫–ª–∏–∫ –ø–æ –ª—é–±–æ–π –∫–∞—Ä—Ç–æ—á–∫–µ —Ñ–∏–ª—å–º–∞
});

// –æ–±—Ä–∞–±–æ—Ç–∫–∞ –∫–ª–∏–∫–∞ –ø–æ header nav
refs.headerNav.addEventListener('click', e => {
  e.preventDefault;
  if (e.target.nodeName === "A") {
    alert;
    changeHeader(e.target.textContent, refs.headerDivToChange); //from "./js/headerNav" –º–µ–Ω—è–µ—Ç —à–∞–ø–∫—É HOME/MY LIBRARY
  };
});

// –æ–±—Ä–∞–±–æ—Ç–∫–∞ –∫–ª–∏–∫–∞ –ø–æ control language
refs.languageLinks.addEventListener('click', e => {
  e.preventDefault;
  if (e.target.nodeName === "use") {
    const lang = e.target.parentNode.parentNode.dataset.set;
    changeLanguage(lang); //from "./js/localization" –º–µ–Ω—è–µ—Ç —è–∑—ã–∫ —Å–∞–π—Ç–∞

  };
});

// –æ–±—Ä–∞–±–æ—Ç–∫–∞ –∫–ª–∏–∫–∞ –ø–æ header logo
refs.headerLogo.addEventListener('click', e => {
  changeHeader('HOME', refs.headerDivToChange); //from "./js/headerNav" –≤—Å–µ–≥–¥–∞ –º–µ–Ω—è–µ—Ç —à–∞–ø–∫—É –Ω–∞ HOME
});

// –æ–±—Ä–∞–±–æ—Ç–∫–∞ –∫–ª–∏–∫–∞ –ø–æ header pagination nav
refs.paginationNav.addEventListener('click', e => {
  e.preventDefault;

  if (e.target.nodeName === "A") {

    onPaginationNavClick(e.target.dataset.number);
  };
});

// –æ–±—Ä–∞–±–æ—Ç–∫–∞ –∫–ª–∏–∫–∞ –ø–æ header developer link
refs.devLink.addEventListener('click', e => {
    e.preventDefault;
    onDevLinkClick(e); //from "./js/footerDevelopers"  –∫–ª–∏–∫ –ø–æ —Å—Å—ã–ª–∫–µ —Ä–∞–∑—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤
});

// –æ–±—Ä–∞–±–æ—Ç–∫–∞ –∫–ª–∏–∫–∞ –ø–æ scroll UP
window.addEventListener('scroll',
  throttle(consts.DEBOUNCE_DELAY, (e) => {
     activateScrollButton(e);//from "./js/btnScrollUp"  –∫–ª–∏–∫ –ø–æ —Å—Å—ã–ª–∫–µ –ø–æ–¥–Ω—è—Ç—å—Å—è –≤–≤–µ—Ä—Ö –≤—Å—Ç—Ä–∞–Ω–∏—Ü—ã
  }));
